<template>
  <section class="banner">
    <h1 :class="{'opacity-0': transparent}" class="uppercase">
      {{ data.judul }}
    </h1>
  </section>
</template>

<script>
export default {
  props: ['data'],
  data: () => ({
    transparent: false
  }),
  methods: {
    handleScroll() {
      if (window.scrollY > 200) {
        this.transparent = true
      } else {
        this.transparent = false        
      }
    }
  },  
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style scoped>
.banner{
  height: 85vh;
  background-image: url('../../assets/img/careers.jpg');
  background-position: center;
  @apply w-screen bg-fixed bg-cover flex items-end p-20;
}
h1{
  @apply text-white text-3xl tracking-wide leading-none duration-200 opacity-100;
}
@screen sm{
  .banner{
    height: 90vh;
  }
  h1{
  @apply text-6xl;
  }
}
</style>